/*---Generell---*/

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif
}

:root{
    --greenColor: #365907;
    --whiteColor: #F7F7F7;
    --purpleColor: #8A199E;
    --pinkColor: #EA28C2;
    --orangeColor: #FF7D35;
    --textColor: #293133;
}



body {
    background-color: var(--whiteColor);
    scroll-behavior: smooth;
}

body h1 {
    font-size: 4vw;
}

body h2 {
    font-size: 3.2vw;
}

body h3 {
    font-size: 1.45vw;
}

body p {
    font-size: 1.35vw;
}


@media only screen and (max-width:1024px) {
    body h1 {
        font-size: 7vw;
    }

    body p {
        font-size: 2vw;
    }

    body h2 {
        font-size: 7vw;
    }

    body h3 {
        font-size: 3.3vw;
    }
}

@media only screen and (max-width:425px) {
    body h1 {
        font-size: 9vw;
    }

    body p {
        font-size: 3.3vw;
    }

    body h2 {
        font-size: 7.1vw;
    }

    body h3 {
        font-size: 4.9vw;
    }
}


.bold {
    font-weight: bold;
}


/*---header---*/

header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 2rem;
    color: var(--greenColor);
}

header img {
    width: 160px;
    object-fit: cover;
}

header nav {
    position: absolute;
    right: 2rem;
}



nav a {
    margin: 0 2rem;
    color: var(--greenColor);
    text-decoration: none;
}

nav a:hover{
    text-decoration: underline;
    cursor: pointer;
}

header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--greenColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
}

@media only screen and (max-width:1024px) {
    header .nav-btn {
        visibility: visible;
        opacity: 1;
    }

    header img {
       width: 130px;
    }

    header nav {
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1.5rem;
        background-color: var(--whiteColor);
        transition: 1s;
        transform: translateY(-100vh);
    }

    header .responsive_nav{
        transform: none;
    }

    nav .nav-close-btn {
        position: absolute;
        top: 2rem;
        right: 2rem;
    }

    nav a {
        font-size: 1.5rem;
    }
}







.color-bar {
    width: 100vw;
    height: 20px;
    background-image: linear-gradient( to right, var(--purpleColor), var(--orangeColor));
}


/*---main---*/

main {
    background-image: url(../images/main.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 500px;
    width: 100vw;
}

.main-content {
    color: white;
    position: absolute;
    left: 6vw;
    top: 18%;
    display: flex;
    flex-direction: column;
    align-items: end;
}

.main-content h1 {
    
}

.main-content p {
    margin-top: 10px;
  
}

@media only screen and (max-width:1024px) {
    main {
        height: 45vw;
    }
}



/*---About---*/

.About {
    height: 45vw;
    display: flex;
    justify-content: center;
    width: 100%;
}

.About-text{
    display: flex;
    justify-content: start;
    flex-direction: column;
   
    width: 50%;
    padding: 6vw;
}

.About h2 {
    color: var(--greenColor);
}

.About p {
    color: var(--textColor);
    margin-top: 25px;
}



.About-img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}

.About-img img {
    height: 500px;
}

@media only screen and (max-width:1024px) {
    .About {
        height: 43vh;
    }

    .About-text{
        width: 100%;
    }

    .About-img {
        display: none;
       
    }
}

@media only screen and (max-width:425px) {
    .About {
        height: 60vh;
    }
    
}


/*---Service---*/

.Service-Section {
    margin-bottom: 9vh;
    height: 38vw;
}

.Our-service {
    width: 100vw;
}

.Our-service h2{
    color: var(--greenColor);
    padding-left: 6vw;
}

.Services {
    display: flex;
    justify-content: space-around;
    height: 320px;
    margin-top: 20px;
    padding-right: 6vw;
}

.Service {
    width: 22%;
    height: 30vw;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border: 5px solid var(--orangeColor);
    border-radius: 20px;
}



.Service img{
    margin-top: 15px;
    width: 12vw;
    margin-bottom: 45px;
}

.Service h3 {
    color: var(--orangeColor);
}

.Service p {
    color: var(--textColor);
    margin-top: 30px;
    text-align: center;
    margin-bottom: 15px;
    margin-right: 15px;
    margin-left: 15px;
}

@media only screen and (max-width:1024px) {
    .Service-Section {
        margin-bottom: 9vh;
        margin-bottom: 10px;
        height: 900px;
    }

    .Services {
        display: flex;
        flex-direction: column;
        height: 90%;
    }

    .Service {
        width: 90%;
        margin-left: 6vw;
        margin-bottom: 2vh;
        height: 30%;
        display: flex;
        justify-content: space-evenly;
        flex-direction: column;
        align-items: center;
        border: 5px solid var(--orangeColor);
        border-radius: 20px;
    }

    .Service img{
        margin-top: 20px;
        width: 10vh;
        margin-bottom: 1%;
    }
}



/*---Why---*/
.Why {
    background-image: url(../images/sea.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 30vw;
    width: 100vw;
}

.Why h2 {
    color: white;
    padding-left: 6vw;
    padding-top: 6vh;
}

.Why-content {
    display: flex;
    padding-left: 6vw;
    padding-right: 6vw;
    margin-top: 10vh;
}   

.onewhy{
    color: white;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding-left: 2vw;
    padding-right: 2vw;
    height: 70%;
}

.onewhy h3{
    
}

.onewhy p {
    margin-top: 2vh;
    text-align: center;
}

@media only screen and (max-width:1024px) {
    .Why {
        height: 700px;
        display: grid;
    }

    .Why-content {
        margin-top: 0;
        display: grid;
        width: 100%;
        padding-left: 24vw;
        padding-right: 24vw;
    } 
    
    .onewhy h3{
        margin-top: 40px;
    }
}

@media only screen and (max-width:425px) {
    .Why {
        height: 600px;
    }
    
}

/*---OurCustomers---*/

.Our-Customors {
    height: 40vw;
    display: flex;
}

.Our-Customors-img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    height: 100%;
}

.Our-Customors-img img{
    height: 500px;
}

.Our-Customors-text {
    display: flex;
    align-items: start;
    justify-content: center;
    padding-left: 6vw;
    flex-direction: column;
    width: 50%;
    height: 100%;
    color: var(--textColor);
}

.Our-Customors-text h2 {
    color: var(--greenColor);
}

.the-costomor {
    width: 30vw;
}

.the-costomor h3 {
    margin-top: 3vh;
}

@media only screen and (max-width:1024px) {

    .Our-Customors {
        height: 750px;
        display: grid;
    }
    .Our-Customors-img{
        display: none;
    }

    
    .Our-Customors-text {
        width: 100%;
       display: grid;
       align-items: center;
    }

    .the-costomor h3 {
        margin-top: 0;
    }
}

@media only screen and (max-width:425px) {
    .Our-Customors {
        height: 60vh;
    }

    .the-costomor {
        width: 80vw;
    }
    
}


/*---Contact---*/


.Contact {
    color: white;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    height: 14vw;
    background-image: linear-gradient( to right, var(--purpleColor), var(--orangeColor));
}

.Contact button {
    margin-top: 10px;
    height: 40px;
    width: 120px;
    color: white;
    background-color: transparent;
    border: 1px solid white;
    border-radius: 5px;
    font-size: 20px;
}

@media only screen and (max-width:1024px) {
    .Contact h2{
        display: none;
    }

    .Contact button {
        margin-top: 0;
        height: 40px;
        width: 120px;
        color: white;
        background-color: transparent;
        border: 1px solid white;
        border-radius: 5px;
        font-size: 20px;
    }
}



/*---Our-Team---*/
.Our-Team{
    height: 40vw;
    padding-top: 6vh;
    padding-left: 6vw;
    padding-right: 6vw;
}

.Our-Team h2 {
    color: var(--greenColor);
}

.Team-Members {
    margin-top: 10px;
    display: flex;
    justify-content: space-between;
}

.Team-Member {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    margin-bottom: 20px;
}

.Team-Members img {
    height: 80px;
    border-radius: 50%;
}

.Team-Member-description {
    margin-left: 10px;
}


.Team-text {
    margin-top: 100px;
    padding-right: 6vw;
}

@media only screen and (max-width:1024px) {
    .Our-Team{
        height: 82vh;
        padding-top: 6vh;
        padding-left: 6vw;
        padding-right: 6vw;
        display: grid;
        text-align: center;
    }

    .Team-Members {
        display: grid;
        justify-content: center;
    }

    .Team-Member {
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin-top: 5px;
        margin-bottom: 20px;
    }

    .Team-Members img {
        height: 50px;
        border-radius: 50%;
    }

    .Team-text {
        margin-top: 50px;
        padding-right: 6vw;
    }
}


.Footer {
    height: 160px;
    color: var(--greenColor);
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
}

.Footer-links a {
    margin-left: 4vw;
    margin-right: 4vw;
    font-size: 1.35vw;
}

.Footer-Socials {
    display: flex;
    justify-content: center;
}

.Footer-Socials img {
    width: 28px;
    margin-left: 5px;
    margin-right: 5px;
}


@media only screen and (max-width:1024px) {
    .Footer {
        height: 200px;
    }

    .Footer-links a {
        display: grid;
        text-align: center;
        margin-left: 4vw;
        margin-right: 4vw;
        font-size: 2vw;
    }

    .Footer-Socials {
        margin-top: 10px;
        display: flex;
        justify-content: center;
    }
}





